import { assign, createMachine } from "xstate";

export const machine = createMachine({
  /** @xstate-layout -H9gvYwuCQCztmzityCBzUIQKABXdBgfkoo6tYWi8XBSV4kGywzyhDDC4TZ4RV6hJ0WPSLSJAA */
  context: {
    token: null,
    cards: [] as any[],
    error: "",
  },
  schema: {
    events: {} as
      | { type: "getEURCard"; data: { type: string } }
      | {
          type: "sendMoney";
          data: {
            beneficiary: string;
            sender: string;
            amount: number;
            donate: boolean;
          };
        },
  },

  initial: "loading cards",
  id: "cardsMachine",
  predictableActionArguments: true,
  states: {
    "loading cards": {
      entry: assign({
        token: (context, event) => JSON.parse(localStorage.getItem("token")!),
      }),
      invoke: {
        id: "getCards",
        src: (context, event) => {
          return fetch(`${process.env.REACT_APP_API_URL}card/cards`, {
            headers: {
              Authorization: `bearer ${context.token}`,
            },
          }).then((response) => response.json());
        },

        onDone: {
          target: "cardsFetched",
          actions: assign({ cards: (context, event) => event.data.cards }),
        },
        onError: {
          actions: assign({
            error: (ctx, ev) => "Something went wrong, try reloading a page",
          }),
          target: "fetchFailure",
        },
      },
    },
    cardsFetched: {
      on: {
        getEURCard: "getting eur card",
        sendMoney: "sending money",
      },
    },
    fetchFailure: {},
    "getting eur card": {
      invoke: {
        id: "getEURCard",
        src: (context, event) =>
          fetch(`${process.env.REACT_APP_API_URL}card/create`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `bearer ${context.token}`,
            },
            body: JSON.stringify(event.data),
          }).then((res) => res.json()),

        onDone: {
          target: "cardsFetched",
          actions: assign({
            cards: (context, event) => [...context.cards, event.data.card],
          }),
        },
        onError: {
          target: "fetchFailure",
        },
      },
    },
    "sending money": {
      invoke: {
        id: "sendMoneyToSomeone",
        src: (context, event) =>
          fetch(`${process.env.REACT_APP_API_URL}card/transaction`, {
            method: "PATCH",
            headers: {
              "Content-Type": "application/json",
              Authorization: `bearer ${context.token}`,
            },
            body: JSON.stringify(event.data),
          }).then((res) => {
            if (res.ok) {
              return res.json();
            } else {
              throw new Error("No beneficiary found");
            }
          }),

        onDone: {
          target: "cardsFetched",
          actions: assign({
            cards: (context, event) =>
              context.cards.map((item) => {
                if (item.cardNumber === event.data.card?.cardNumber) {
                  return {
                    ...item,
                    balance: event.data.card.balance,
                    transactions: event.data.card.transactions,
                  };
                }
                return item;
              }),
          }),
        },
        onError: {
          actions: assign({ error: "No beneficiary found" }),
          target: "cardsFetched",
        },
      },
    },
  },
});
